.draggable-container {
  /* width: 200px;
  height: 200px; */
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
  cursor: grab;
  position: fixed; /* Set default position to fixed */
}

/* Position at top-left */
.draggable-container.top-left {
  top: 10px;
  left: 10px;
}

/* Position at top-right */
.draggable-container.top-right {
  top: 10px;
  right: 10px;
}

/* Position at bottom-left */
.draggable-container.bottom-left {
  bottom: 10px;
  left: 10px;
  top: unset; /* Reset the 'top' property */
}

/* Position at bottom-right */
.draggable-container.bottom-right {
  bottom: 10px;
  right: 10px;
  top: unset; /* Reset the 'top' property */
}

/* Position at center */
.draggable-container.center {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.draggable-container.closed {
  opacity: 0;
}

.draggable-container:not(.closed) {
  cursor: grab;
}

.draggable-container:not(.closed):hover .draggable-button {
  cursor: grab;
}

.draggable-container.closed,
.draggable-container:not(.closed):hover {
  cursor: default;
}

/* SearchBar.css */
.search-bar {
  position: relative;
  width: 500px; /* Set your desired width */
  margin: 1px auto; /* Adjust the margin as needed */
}

input[type="text"] {
  margin: auto;
  width: 95%;
  padding: 10px;
  font-size: 16px;
  border: 2px solid black;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
}

input[type="text"]:focus {
  border-color: balck; /* Change the color as per your preference */
  box-shadow: 0 0 5px #000000;
}
